import React from "react";
import { Container, Row, Tab, Col, Nav } from "react-bootstrap";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import { Link } from "gatsby";
const MLMIntegration = (props) => {
  const { db } = props;
  return (
    <>
      <Container>
        <div className="web_dev_tabs_wrapper">
          <Row>
            <h2 className="text-center">
              Our Blockchain-based MLM software Integrations
            </h2>
          </Row>
          <Tab.Container id="left-tabs-example" defaultActiveKey="one">
            <Row className="smart_ct_row web_dev_tabs">
              <Col sm={4} className="d-flex justify-content-center">
                <Nav variant="pills" className="flex-column">
                  <Nav.Item>
                    <Nav.Link eventKey="one">Multi-currency Wallet</Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="two">
                      API’s Integration to <br />
                      External Exchanges
                    </Nav.Link>
                  </Nav.Item>
                  <Nav.Item>
                    <Nav.Link eventKey="three">Investment module</Nav.Link>
                  </Nav.Item>
                </Nav>
              </Col>
              <Col sm={8}>
                <Tab.Content>
                  <Tab.Pane eventKey="one">
                    <div className="web_dev_tab_content">
                      <h3 className="text-dark">Multi-currency Wallet</h3>
                      <p>
                        It makes crypto transfers and sends referrals
                        seamlessly. The integrated chat feature allows easy
                        sending and receiving of crypto assets. It offers
                        high-level security, user-controlled private keys, quick
                        transactions, and easy referral link sharing.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                  <Tab.Pane eventKey="two">
                    <div className="web_dev_tab_content">
                      <h3 className="text-dark">
                        API’s Integration to External Exchanges
                      </h3>
                      <p>
                        We created a secure API with top exchanges like Binance
                        and Huobi. Users can easily buy, sell, and join MLM
                        without giving away their funds. Our global liquidity
                        connections ensure quick transactions with multi-layer
                        security.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                  <Tab.Pane eventKey="three">
                    <div className="web_dev_tab_content">
                      <h3 className="text-dark">Investment module</h3>
                      <p>
                        Boost your earnings with an easy investment module.
                        Users can easily join MLM, and experience fast fund
                        growth. It's fully autonomous, and transactions are
                        secure with strong smart contracts. The complete process
                        is transparent.
                      </p>
                      <div className="crypto_btns_grp">
                        <Link className="prime_submit_btn" to="/contact-us">
                          View Live Demo
                        </Link>
                        <button className="second_consult_btn" type="button">
                          <TalkToUsNew label="Free Consult" db={db} />
                        </button>
                      </div>
                    </div>
                  </Tab.Pane>
                </Tab.Content>
              </Col>
            </Row>
          </Tab.Container>
        </div>
      </Container>
    </>
  );
};

export default MLMIntegration;
